

















import {addJoin} from 'common/js/requestUrl'
import {request,showToast} from 'common/js/common'
export default {
	data() {
		return {
			name: '',
			phone: '',
			address: '',
			msg: '',
		};
	},
	methods: {
		inputName(e){
			this.name = e.detail.value
		},
		inputPhone(e){
			this.phone = e.detail.value
		},
		inputAdd(e){
			this.address = e.detail.value
		},
		inputMsg(e){
			this.msg = e.detail.value
		},
		submitData(){
			if(!this.name){
				showToast('请先输入名称')
				return
			}
			if(!this.phone){
				showToast('请先输入手机号')
				return
			}
			if(!this.address){
				showToast('请先输入地址')
				return
			}
			if(!this.msg){
				showToast('请先输入留言内容')
				return
			}
			
			request(addJoin,{
				phone: this.phone,
				name: this.name,
				address: this.address,
				msg: this.msg
			},(res) => {
				if(res.code == 200){
					showToast('提交成功','success')
					this.name = ''
					this.address = ''
					this.msg = ''
					this.phone = ''
				}
			})
		}
	}
}
